export function initMobile_DashBoard(API_BASE) {
    const section = document.getElementById("dashboard");
    if (!section)
        return;
    const timeEl = section.querySelector("#currentTime");
    function updateTime() {
        const now = new Date();
        timeEl.textContent = now.toLocaleTimeString("ko-KR", { hour12: false });
    }
    updateTime();
    setInterval(updateTime, 1000);
    console.log("üè† [Mobile_DashBoard] Ìôà Ï¥àÍ∏∞Ìôî ÏôÑÎ£å");
}
