// ======================================================
// üì± Ï†ïÌò∏Í∞úÎ∞ú - Î™®Î∞îÏùº SET-UP ÌôîÎ©¥ Ï¥àÍ∏∞Ìôî
// ÏûëÏÑ±Ïûê: Ïó¨ÌÉúÍ≤Ä
// ======================================================
export function initMobile_SetUp(API_BASE) {
    console.log("üöÄ [SET-UP] Ï¥àÍ∏∞Ìôî ÏãúÏûë");
    // ÌôîÎ©¥ ÏöîÏÜå Ï∞æÍ∏∞
    const section = document.getElementById("mobile_set_up_section");
    const tabBtn = document.querySelector("[data-tab='mobile_set_up']");
    const orderSelect = document.getElementById("setupOrderSelect");
    const btnLoadYesterday = document.getElementById("btnLoadYesterdayWork");
    const percentRange = document.getElementById("setupProgressPercent");
    const percentLabel = document.getElementById("setupProgressPercentLabel");
    const loadedWorkBox = document.getElementById("setupLoadedWork");
    const workInput = document.getElementById("setupWorkInput");
    if (!section || !tabBtn) {
        console.warn("‚ö†Ô∏è [SET-UP] section ÎòêÎäî Î≤ÑÌäºÏùÑ Ï∞æÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
        return;
    }
    // ======================================================
    // üìå 1) ÌÉ≠ ÌÅ¥Î¶≠ Ïãú ÌôîÎ©¥ Ï†ÑÌôò
    // ======================================================
    tabBtn.addEventListener("click", () => {
        document.querySelectorAll("section.tab-section, section[id^='mobile_']")
            .forEach(sec => sec.classList.add("hidden"));
        section.classList.remove("hidden");
        window.scrollTo(0, 0);
    });
    // ======================================================
    // üìå 2) ÏàòÏ£ºÍ±¥ Î™©Î°ù Î°úÎìú (ÏÑúÎ≤Ñ Ïó∞Îèô)
    // ======================================================
    async function loadOrders() {
        try {
            orderSelect.innerHTML = `<option value="">Î∂àÎü¨Ïò§Îäî Ï§ë...</option>`;
            const res = await fetch(`${API_BASE}/api/mobile/orders`, { method: "GET" });
            const data = await res.json();
            orderSelect.innerHTML = `<option value="">ÏàòÏ£ºÍ±¥ÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî</option>`;
            data.forEach((o) => {
                const opt = document.createElement("option");
                opt.value = o.id;
                opt.textContent = `${o.project_name} (${o.customer})`;
                orderSelect.appendChild(opt);
            });
        }
        catch (err) {
            console.error("‚ùå ÏàòÏ£ºÍ±¥ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:", err);
            orderSelect.innerHTML = `<option value="">Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®</option>`;
        }
    }
    // ======================================================
    // üìå 3) Ï†ÑÎÇ† ÏóÖÎ¨¥ Î∂àÎü¨Ïò§Í∏∞
    // ======================================================
    btnLoadYesterday.addEventListener("click", async () => {
        const orderId = orderSelect.value;
        if (!orderId) {
            alert("‚ö†Ô∏è Î®ºÏ†Ä ÏàòÏ£ºÍ±¥ÏùÑ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.");
            return;
        }
        btnLoadYesterday.textContent = "Î∂àÎü¨Ïò§Îäî Ï§ë...";
        btnLoadYesterday.disabled = true;
        try {
            const res = await fetch(`${API_BASE}/api/mobile/set-up/yesterday?order_id=${orderId}`);
            const data = await res.json();
            loadedWorkBox.textContent = data.text || "Ï†ÑÎÇ† ÏóÖÎ¨¥ ÎÇ¥Ïö©Ïù¥ ÏóÜÏäµÎãàÎã§.";
            percentRange.value = data.percent || "0";
            percentLabel.textContent = `(${percentRange.value}%)`;
        }
        catch (err) {
            console.error("‚ùå Ï†ÑÎÇ† ÏóÖÎ¨¥ Î∂àÎü¨Ïò§Í∏∞ Ïò§Î•ò:", err);
            loadedWorkBox.textContent = "Ï†ÑÎÇ† ÏóÖÎ¨¥Î•º Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.";
        }
        finally {
            btnLoadYesterday.textContent = "Ï†ÑÎÇ† ÏóÖÎ¨¥ Î∂àÎü¨Ïò§Í∏∞";
            btnLoadYesterday.disabled = false;
        }
    });
    // ======================================================
    // üìå 4) ÏßÑÌñâÎ•† Range ‚Üí Label Î∞òÏòÅ
    // ======================================================
    percentRange.addEventListener("input", () => {
        percentLabel.textContent = `(${percentRange.value}%)`;
    });
    // ======================================================
    // üìå 5) ÌôîÎ©¥ Ï¥àÍ∏∞ ÏÑ§Ï†ï
    // ======================================================
    loadOrders(); // ÏàòÏ£ºÍ±¥ ÏûêÎèô Î∂àÎü¨Ïò§Í∏∞
    console.log("‚úÖ [SET-UP] Ï¥àÍ∏∞Ìôî ÏôÑÎ£å");
}
