const isLocal = location.hostname === "localhost" ||
    location.hostname === "127.0.0.1" ||
    location.hostname.includes("app.github.dev");
const API_BASE = isLocal
    ? "http://127.0.0.1:5050"
    : "https://port-0-innomax-mghorm7bef413a34.sel3.cloudtype.app";
console.log("üì± [Mobile_Workspace] Ï¥àÍ∏∞Ìôî ÏãúÏûë");
// ======================================================
// üë§ Î°úÍ∑∏Ïù∏ Ïù∏Ï¶ù Î∞è ÏÑ∏ÏÖò Í¥ÄÎ¶¨
// ======================================================
function initAuthAndUserInfo() {
    const userData = localStorage.getItem("user");
    if (!userData) {
        alert("ÏÑ∏ÏÖò ÎßåÎ£å ÎòêÎäî ÎπÑÏ†ïÏÉÅ Ï†ëÍ∑ºÏûÖÎãàÎã§.");
        window.location.href = "index.html"; // ‚úÖ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
        return false;
    }
    const user = JSON.parse(userData);
    const userName = document.getElementById("userName");
    const userRole = document.getElementById("userRole");
    if (userName)
        userName.textContent = user.name;
    if (userRole)
        userRole.textContent = "SWÌåÄ";
    // ‚úÖ ÏÑ∏ÏÖò ÎßåÎ£å Í≤ÄÏÇ¨ (30Î∂Ñ)
    const loginTime = user.loginTime;
    const now = Date.now();
    if (now - loginTime > 1000 * 60 * 30) {
        alert("ÏÑ∏ÏÖòÏù¥ ÎßåÎ£åÎêòÏóàÏäµÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏ Ìï¥Ï£ºÏÑ∏Ïöî.");
        localStorage.clear();
        window.location.href = "index.html";
        return false;
    }
    // ‚úÖ Î°úÍ∑∏ÏïÑÏõÉ Î≤ÑÌäº Ïù¥Î≤§Ìä∏
    const logoutBtn = document.getElementById("btnLogout");
    if (logoutBtn) {
        logoutBtn.addEventListener("click", async () => {
            const ok = confirm("Ï†ïÎßê Î°úÍ∑∏ÏïÑÏõÉ ÌïòÏãúÍ≤†ÏäµÎãàÍπå?");
            if (!ok)
                return;
            localStorage.clear();
            try {
                await fetch(`${API_BASE}/api/logout`, {
                    method: "POST",
                    credentials: "include",
                });
            }
            catch {
                /* Î¨¥Ïãú */
            }
            window.location.href = "index.html";
        });
    }
    // ‚úÖ Îí§Î°úÍ∞ÄÍ∏∞ Î∞©ÏßÄ
    history.pushState(null, "", location.href);
    window.onpopstate = function () {
        history.go(1);
    };
    return true;
}
