// üì± Î©îÏù∏ Ï¥àÍ∏∞Ìôî
// ======================================================
export async function initMobile_Workspace() {
    try {
        Mobile_Loading.show("Î°úÎî© Ï§ë...");
        // ‚úÖ Î°úÍ∑∏Ïù∏/ÏÑ∏ÏÖò Í≤ÄÏ¶ù
        const ok = initAuthAndUserInfo();
        if (!ok)
            return;
        // ‚úÖ Í∞Å ÌÉ≠ Ï¥àÍ∏∞Ìôî
        console.log("üß≠ [Mobile_Workspace] Î™®Îìà Ï¥àÍ∏∞Ìôî ÏãúÏûë");
        initMobile_DashBoard(API_BASE);
        console.log("‚úÖ [Mobile_Workspace] Î™®Îì† Î™®Îìà Ï¥àÍ∏∞Ìôî ÏôÑÎ£å");
        // ‚úÖ ÏÑúÎ≤Ñ ÏÉÅÌÉú Î™®ÎãàÌÑ∞ÎßÅ ÏãúÏûë
        startServerConnectionCheck();
        // üîπ Ï∂úÏû•/ÏÇ¨ÎÇ¥ ÏóÖÎ¨¥ Pool Î™®Îìà Ï¥àÍ∏∞Ìôî
        initBusinessModules();
    }
    catch (err) {
        console.error("‚ùå [Mobile_Workspace] Ï¥àÍ∏∞Ìôî Ïò§Î•ò:", err);
        alert("Î™®Î∞îÏùº ÏõåÌÅ¨Ïä§ÌéòÏù¥Ïä§ Ï¥àÍ∏∞Ìôî Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    }
    finally {
        Mobile_Loading.hide();
    }
}
// ======================================================
// üì¶ DOM Î°úÎìú ÌõÑ ÏûêÎèô Ïã§Ìñâ
// ======================================================
document.addEventListener("DOMContentLoaded", () => {
    initMobile_Workspace();
});
// ======================================================
// üåê ÏÑúÎ≤Ñ Ïó∞Í≤∞ ÏÉÅÌÉú Ï≤¥ÌÅ¨ (Footer ÌëúÏãú)
// ======================================================
async function checkServerStatus() {
    try {
        const res = await fetch(`${API_BASE}/api/health`, { cache: "no-store" });
        if (!res.ok)
            throw new Error("HTTP " + res.status);
        return true;
    }
    catch {
        return false;
    }
}
function updateFooterStatus(connected) {
    const el = document.getElementById("serverStatus");
    if (!el)
        return;
    if (connected) {
        el.textContent = "ÏÑúÎ≤Ñ Ïó∞Í≤∞Îê®";
        el.classList.remove("before:text-red-500", "text-gray-400");
        el.classList.add("before:text-green-400", "text-green-300");
    }
    else {
        el.textContent = "ÏÑúÎ≤Ñ Ïó∞Í≤∞ ÎÅäÍπÄ";
        el.classList.remove("before:text-green-400", "text-green-300");
        el.classList.add("before:text-red-500", "text-gray-400");
    }
}
// ======================================================
// üì¶ Ï∂úÏû•ÏóÖÎ¨¥ + ÏÇ¨ÎÇ¥ÏóÖÎ¨¥ Pool Ï¥àÍ∏∞Ìôî
// ======================================================
function initBusinessModules() {
    console.log("üì¶ [Mobile_Workspace] ÏóÖÎ¨¥ Pool Ï¥àÍ∏∞Ìôî ÏãúÏûë");
    initMobile_SetUp(API_BASE);
    initMobile_AS(API_BASE);
    initMobile_Test(API_BASE);
    initMobile_Doc(API_BASE);
}
/**
 * 5Ï¥à Í∞ÑÍ≤©ÏúºÎ°ú ÏÑúÎ≤Ñ Ïó∞Í≤∞ ÏÉÅÌÉúÎ•º Ï†êÍ≤ÄÌïòÍ≥† footerÏóê ÌëúÏãú
 */
function startServerConnectionCheck() {
    checkServerStatus().then(updateFooterStatus);
    setInterval(async () => {
        const ok = await checkServerStatus();
        updateFooterStatus(ok);
    }, 5000);
}
// ======================================================
// üì± ÌÉ≠ Ï†ÑÌôò Ï≤òÎ¶¨ (PC workspace.ts Ï∞∏Í≥†ÌïòÏó¨ ÎèôÏùº Íµ¨Ï°∞Î°ú Ï∂îÍ∞Ä)
// ======================================================
document.addEventListener("DOMContentLoaded", () => {
    initMobile_Workspace();
    // üîπ ÌÉ≠ Î≤ÑÌäºÎì§: data-tab ÏÜçÏÑ± ÌïÑÏàò
    const tabButtons = document.querySelectorAll("[data-tab]");
    console.log("üì± [Mobile_Workspace] Îç∞Ïù¥ÌÑ∞-ÌÉ≠ Î≤ÑÌäº Ïàò:", tabButtons.length);
    tabButtons.forEach((btn) => {
        btn.addEventListener("click", () => {
            const targetId = btn.dataset.tab;
            if (!targetId)
                return;
            console.log(`[MOBILE TAB] ÌÅ¥Î¶≠Îê® ‚Üí ${targetId}`);
            // üîπ Î™®Îì† Î™®Î∞îÏùº Ìå®ÎÑê Ïà®Í∏∞Í∏∞
            document
                .querySelectorAll("[id^='mobile_panel-']")
                .forEach((el) => el.classList.add("hidden"));
            // üîπ Ìï¥Îãπ Ìå®ÎÑê ÌëúÏãú
            const panel = document.getElementById(`mobile_panel-${targetId}`);
            if (!panel) {
                console.error(`[MOBILE TAB] Ìå®ÎÑê ÏóÜÏùå: mobile_panel-${targetId}`);
                return;
            }
            panel.classList.remove("hidden");
            // üîπ Î™®Î∞îÏùºÏóêÏÑúÎäî sidebar ÏûêÎèô Îã´Í∏∞ (UI Í≤ΩÌóò Í∞úÏÑ†)
            const sidebar = document.getElementById("mobileSidebar");
            if (sidebar)
                sidebar.classList.add("hidden");
        });
    });
});
